<template>
  <div id="bf-card">
    <div id="header-wrap">
      <div class="bf-headline">{{ name }}</div>
      <v-icon id="doc-status" :color="iconColor">{{ icon }}</v-icon>
    </div>
    <v-layout row>
      <v-flex xs12 md3>
        <div id="bf-card-body">
          <a id="component-link" :href="formattedUrl">
            <button id="view-button" :disabled="disabled">View</button>
          </a>
        </div>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
  export default {
    props: {
      name: String,
      description: String,
      path: String,
      docs: Boolean
    },
    computed: {
      formattedUrl() {
        // FIXME: naive approach, should use regex pattern
        const pathSplit = this.path.split('/')
        const path = pathSplit.slice(0, pathSplit.length - 1).join('/');
        return `http://localhost:3000/${path}`
      },
      icon() {
        if (this.docs) return 'check_circle'
        return 'report'
      },
      iconColor() {
        if (this.docs) return 'green'
        return 'red'
      },
      disabled() {
        if (this.docs) return false
        return true
      }
    },
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #doc-status {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  #component-link {
    text-decoration: none;
  }

  #view-button {
    border: 1px solid #bdbdbd;
    border-radius: 5px;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    margin: 0;
    padding: 9px 35px;
    text-transform: none;
    background: #2760FF;
    border-color: #2760FF;
    color: white;
  }

  #view-button:disabled {
    background:rgba(211,211,211,0.5);
    border-color: rgba(211,211,211,0.5);
  }

  #bf-card {
    background: #ffffff;
    border-radius: 2px;
    border: 1px solid #dadada;
    display: block;
  }

  #bf-card-body {
    padding: 20px;
  }

  #header-wrap {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -ms-flex-direction: row;
    -webkit-flex-direction: row;
    flex-direction: row;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    border-bottom: 1px solid #DADADA;
    padding: 20px 20px 18px;
  }

  .bf-headline {
    font-size: 16px;
    text-align: left;
  }

</style>



