import Vue from 'vue';
import Router from 'vue-router';
import WebComponentList from '@/components/WebComponentList';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: WebComponentList,
    },
  ],
});



