<template>
  <v-container grid-list-md fluid>
    <v-layout class="mb-4" row justify-center>
      <v-flex xs4>
        <v-text-field
          name="search"
          placeholder="Search for components by name"
          autofocus
          single-line
          class="input-group--focused"
          v-model="searchText"
        ></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout v-if="isLoading">
      <v-flex xs12>
        <v-progress-circular indeterminate v-bind:size="50" color="primary"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout v-if="!isLoading" class="mb-4" row wrap>
      <v-flex xs12 md3>
        <h2 class="text-xs-left">Number of Components: {{totalComponents}} </h2>
      </v-flex>
      <v-flex xs12 md3>
        <h2 class="text-xs-left">Component Pages: {{coverage}} </h2>
      </v-flex>
    </v-layout>
    <v-layout v-if="!isLoading" row wrap>
      <v-flex class="mb-4" xs12 md3 v-for="(item, index) in searchResults" :key="item.name">
        <web-component
          v-bind="item"
          v-bind:index="index"
        ></web-component>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    created() {
      fetch('./static/component_index.json')
        .then((response) => {
          if (response.ok) {
            return response.json();
          }

          throw new Error(response);
        })
        .then((json) => {
          this.elements = json.components;
          this.searchResults = json.components;
          this.isLoading = false;
        })
        .catch((err) => {
          console.error(err);
          this.isLoading = false;
        });
    },
    data() {
      return {
        searchText: "",
        searchResults: [],
        isLoading: true,
        elements: [
          {
            name: "blackfynn-analytics",
            path: "src/components/blackfynn/analytics/analytics.html",
          },
          {
            name: "blackfynn-data-simple-input",
            path: "src/components/blackfynn/data/simple-input.html",
          },
          {
            name: 'bf-search-input',
            path: 'src/components/blackfynn/data/bf-search-input/bf-search-input.html',
          },
          {
            name: "blackfynn-upload-file",
            path: "src/components/blackfynn/upload/file.html",
          },
        ],
      };
    },
    watch: {
      searchText(value) {
        this.filterElements(value)
      }
    },
    methods: {
      filterElements(searchText) {
        const filtered = this.elements.filter((element) => {
          return element.name.includes(searchText)
        })
        this.searchResults = filtered;
      }
    },
    computed: {
      totalComponents() {
        return this.elements.length;
      },
      coverage() {
        const elements = this.elements
        let haveDocs = 0

        for (let x = 0; x < elements.length; x++) {
            if (elements[x].docs) haveDocs += 1
        }
        const pct = Math.floor(haveDocs / elements.length * 100)
        return `${haveDocs} / ${elements.length} (${pct}%)`
      }
    },
  };
</script>

